body {
  font-family: "Hanken Grotesk", sans-serif;
}

nav {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background: #0e0e0e;
  width: 100%;
  height: 83px;
  position: fixed;
  z-index: 5;
  transition: all 0.5s;
  color: #ffffff;
}

.nav-scrolled {
  background-color: #24252a;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
}

.checkbtn {
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}

.checkbtn img {
  width: 30px;
  height: 30px;
}

#check {
  display: none;
}

nav .logo img {
  margin: 0 100px;
}

nav ul {
  padding: 0;
  list-style: none;
  float: right;
  margin: 0 20px 0 0;
}

nav ul li {
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
}

nav ul li a {
  text-decoration: none;
  color: var(--tittle-letter);
  font-size: 17px;
  text-transform: uppercase;
  padding: 7px 13px;
  border-radius: 3px;
}

.navbar a:hover {
  background: #1b9bff;
  transition: 0.5s;
  cursor: pointer;
}

.logo {
  width: 74px;
  margin-top: 14px;
}

@media (max-width: 768px) {

  /*Responsive navbar*/
  .checkbtn {
    display: block;
  }

  .logo {
    width: 66px;
  }

  nav ul {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(33, 49, 63, .95);
    top: 82px;
    left: -200%;
    text-align: center;
    transition: all .5s;
  }

  nav ul li {
    display: block;
  }

  a:hover,
  a.active {
    background: none;
  }

  #check:checked~ul {
    left: 0;
  }
}
