/**/
.button {
    width: 90px;
    height: 40px;
    font-size: 16px;
    letter-spacing: 1.3px;
    font-weight: 700;
    color: #313133;
    background: #fafcfd;
    border: none;
    border-radius: 1000px;
    box-shadow: 12px 12px 24px rgba(115, 115, 115, 0.64);
    transition: all 0.3s ease-in-out 0s;
    outline: none;
    position: relative;
    padding: 10px;
}

.button::after {
    content: '';
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 2px solid #000000;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: ring 1.5s infinite;
            animation: ring 1.5s infinite;
}

@-webkit-keyframes ring {
    0% {
        width: 100px;
        height: 50px;
        opacity: 1;
    }

    100% {
        width: 90px;
        height: 40px;
        opacity: 0;
    }
}

@keyframes ring {
    0% {
        width: 100px;
        height: 50px;
        opacity: 1;
    }

    100% {
        width: 90px;
        height: 40px;
        opacity: 0;
    }
}

.back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background: #f2f4f5;
    box-shadow: 0 5px 50px rgb(32 35 41 / 15%);
    color: rgb(0, 0, 0);
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    border-radius: 6px;
}

/**/
.btn-modal:hover {
    cursor: pointer
}

.btn-modal {
    background: transparent;
    outline: none;
    position: relative;
    border: 2px solid #111;
    padding: 15px 50px;
    overflow: hidden;
}

.btn-modal:hover:before {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}

.btn-modal:before {
    content: attr(data-hover);
    position: absolute;
    top: 1.1em;
    left: 0;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 800;
    font-size: .8em;
    opacity: 0;
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
    transition: all .3s ease-in-out;
}

/*.btn-modal div (.btn-modal text before hover)*/
.btn-modal:hover div {
    opacity: 0;
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0)
}

.btn-modal div {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 800;
    font-size: .8em;
    transition: all .3s ease-in-out;
}

